const Evento = (props) => {
    const {url, name, description} = props.evento;

    let desc = description.text;
    if(desc){
        desc = desc.substr(0, 250) + ' ...';
    }
    
    return ( 
        <a href={url} className="list-group-item active text-light mb-1" target="_blank">
            <h3 className="mb-3">{name.text}</h3>
            <p className="mb-1">{desc}</p>
        </a>
     );
}
 
export default Evento;